function filtrarData() {
    var datos = COPIA_DATA;
    var resultado = [];

    for (var i = datos.length - 1; i >= 0; i--) {
        var data = datos[i];
        var keyMedida = data["resource"]["name"];
        //medidas 
        //var excludeStart = (this.date === null) ? true : false;
        //var excludeEnd = (this.date1 === null) ? true : false;
        var medidaValida = (this.medidasSeleccionadas.length === 0) ? true :
            (this.medidasSeleccionadas.indexOf(keyMedida) !== -1 ? true : false);
        var tagValido = true;
        // if()
        var tagValido = (this.tagsSeleccionadas.length === 0) ? true :
            (this.tagsSeleccionadas.indexOf(data["tags"]) !== -1 ? true : false);
        //var rango = moment.range(this.date, this.date1);
        //var estaEntreFecha = rango.contains(moment(data["effectiveDate"], 'MM/DD/YYYY'), 
        //    { "excludeStart": excludeStart, "excludeEnd": excludeEnd });
        console.log(medidaValida);
        if (medidaValida) {
            resultado.push(data);
        }
    }
    var mislbls = [];
    for (var i = resultado.length - 1; i >= 0; i--) {
        var fila = resultado[i];
        var keyMedida = fila["resource"]["name"];
        if (mislbls.indexOf(keyMedida) === -1) {
            mislbls.push(keyMedida);
        }
    }
    console.log("medidas lbl:::")
    console.log(mislbls);
    this.lblMedidas = mislbls;
    this.desserts = resultado;
    return resultado;
}

function filtrarDataMedidas(filtrando) {
    for (var i = filtrando.length - 1; i >= 0; i--) {
        filtrando[i];
    }
}

function labelsFromData2(array, label, label2) {
    var resultado = [];
    for (var i = array.length - 1; i >= 0; i--) {
        var fila = array[i];
        var key = fila[label][label2];
        var encontrado = resultado.indexOf(key);
        if (encontrado === -1) {
            resultado.push(key);
        }
    }
    return resultado;
}


var mismetodos = {
    "filtrarData": filtrarData,
    "fechaFormateada": function(fechaStr) {
        return fechaStr ? moment(fechaStr.replace("Z", "")).format('DD/MM/YYYY') : ''
    },
    "llenarInformacion": function() {
        console.log("creating");
        var vue = this;
        var margen = (usuario["margen"] ?usuario["margen"] : 0 ) / 100;
        var authHeader = "Bearer " + token;
        var authHeaderObt = { "headers": { "Authorization": authHeader } };

        var URI_COST = URI_AZURE + URI_TOTAL_BILLING;
        URI_COST = URI_COST.replace("{{CUSTOMER_ID}}", CUSTOMER_ID);
        axios.get(URI_COST, authHeaderObt).then(function(response) {
            // console.log("waiting");
            //var URI = URI_AZURE + "ratecards/azure";
            var response = response.data;
            var fechaInicio = response["billingStartDate"].replace("Z", "");
            var fechaFin = response["billingEndDate"].replace("Z", "");
            var totalx =  (response["afterTaxTotal"] * margen) + (response["afterTaxTotal"] * margen);
            vue.mitotal = totalx;
            var URI = URI_AZURE + URI_UTILIZATION;
            URI = URI.replace("{{CUSTOMER_ID}}", CUSTOMER_ID);
            URI = URI.replace("{{SUBSCRIPTION_ID}}", SUBSCRIPTION_ID);

           // var fechaInicio = moment(this.date).format("YYYY-MM-DD");
            //var fechaFin = moment(this.date1).format("YYYY-MM-DD");
           // fechaInicio = fechaInicio + "T00%3A00%3A00";
            //fechaFin = fechaFin + "T00%3A00%3A00";
            URI = URI.replace("{{START_TIME}}", fechaInicio);
            URI = URI.replace("{{END_TIME}}", fechaFin);

            // var URI_AZURE = "https://api.partnercenter.microsoft.com/v1/";
            axios.get(URI, authHeaderObt)
                .then(response => {
                    var copia = [];
                    var meters = response.data.items;
                    var lent = meters.length;
                    var costUnit = (totalx / meters.length );
                    
                    for (var i = lent - 1; i >= 0; i--) {
					meters[i]["Costo"] = costUnit;
                        //var metro1 = meters[i];
                       // copia.push(meters[i]);
                    }
                    vue.desserts = meters;
                    COPIA_DATA = vue.desserts.slice(0);

                    vue.lblMedidas = labelsFromData2(vue.desserts, "resource", "name").sort();
                    vue.medidas = vue.lblMedidas.slice(0);
                    // this.tags = labelsFromData(this.desserts, "instanceData", "tags").sort();
                    vue.filtrarData();
                    //  console.log("response:::");
                    //  console.log(response.data);
                })
                .catch((error) => {
                    console.log("mi error " + error);
                });
        });

    }
};
